<template>
    <div class='proposal'>
        <div class='proposal-word'>
            <!--打印按钮-->
            <h4>诊疗建议</h4>
            <div>
                <div class='row'>
                    <div class='col-md-3'>
                        <span>姓名：</span>
                        <span>{{clicnChart.patientName}}</span>
                    </div>
                    <div class='col-md-3'>
                        <span>性别：</span>
                        <span>{{clicnChart.sex}}</span>
                    </div>
                    <div class='col-md-3'>
                        <span>年龄：</span>
                        <span>{{clicnChart.age}}岁</span>
                    </div>
                    <div class='col-md-3'>
                        <span>编号：</span>
                        <span>{{clicnChart.clinicsNo}}</span>
                    </div>
                </div>
                <div class='row'>
                    <div class='col-md-3'>
                        <span>主诉疾病：</span>
                        <span>{{clicnChart.type}}</span>
                    </div>
                    <div class='col-md-4'>
                        <span>治疗医生：</span>
                        <span>{{clicnChart.doctorName}}</span>
                    </div>
                </div>
                <div class='row'>
                    <div class='col-md-6 clearfix'>
                        <div class='pull-left'>
                            诊断意见：
                        </div>
                        <div class='pull-left'>
                            {{clicnChart.diagnosisMemo}}
                        </div>
                    </div>
                </div>
                <div class='row'>
                    <div class='col-md-12'>
                        治疗建议：{{clicnChart.treatmentMemo}}
                    </div>
                </div>
                <div class='P-bottomBorder'></div>
                <div class='P-bottomData'>
                    <div class='pull-left'>{{clicnChart.createdWhen}}</div>
                    <div class='pull-right'>
                        <span>协诊专家</span>
                        <span>{{clicnChart.expertName}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            clicnChart: {},
            OrderChart: {}
        }
    },
    created() {
        this.clicnChart = JSON.parse(localStorage.getItem('clicnChart'));
        this.OrderChart = JSON.parse(localStorage.getItem('OrderChart'));
    },
    mounted() {
        this.print()
    },
    methods: {
        print() {
            if (document.execCommand("print")) {
                this.$router.go(-1)
            }
        }
    }
}

</script>
<style scoped>
.proposal .proposal-word {
    margin: 15px auto;
    border: solid 1px #ddd;
    width: 700px;
    height: 700px;
    padding: 0px 20px;
    position: relative;
}

.proposal .proposal-word h4 {
    text-align: center;
    height: 50px;
    line-height: 50px;
    margin-bottom: 15px;
    border-bottom: solid 1px #DDD;
}

.proposal .proposal-word .row {
    margin-bottom: 20px;
}

.proposal .P-bottomBorder {
    width: 658px;
    background: #DDD;
    height: 1px;
    position: absolute;
    bottom: 70px;
    left: 22px;
}

.proposal .P-bottomData {
    position: absolute;
    bottom: 40px;
    left: 22px;
    width: 658px;
}

.proposal .print-btn {
    position: absolute;
    top: 10px;
    right: -70px;
}
</style>



